.articleContainer {
    background-color: white;
    border: 4px solid;
    border-color: rgb(255, 123, 0);
    border-radius: 20px;
    margin: 10px;
    padding: 15px;
    width: 65%;
}
.imageContainer {
    float: left;
    width: 30%;
    margin-right: 15px;
}
.articleImage {
    width: 100%;
    margin-bottom: 3px;
}
.imageCaption {
    margin-top: 0px;
    text-align: center;
    font-size: 12px;
    font-style: italic;
}
.articleTitle {
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}
.articleBody {
    white-space: pre-wrap;
}